import React from "react";
import Footer from "../../commons/Footer";
import Header from "../../commons/Header";
import {
  AntInput,
  AntPhone,
} from "../../../components/Basic/AntFields";
import { Field, Formik, Form } from "formik";
import ButtonBase from "../../../components/button";
import {
  validationMessage,
  MAX_LENGHT_30,
  MIN_LENGHT_6,
  patterns,
} from "../../../common/validates";
import * as Yup from "yup";
import { ConfigProvider } from "antd-country-phone-input";
import en from "world_countries_lists/data/en/world.json";
import Partners from "../../../components/Partners";
import axios from "axios";
import CountDown from "../../../components/CountDown";
import GettingStartedContentHeading from "./commons/GettingStartedContentHeading";
import GettingStartedContentList from "./commons/GettingStartedContentList";
import VideoContainer from "./commons/VideoContainer";
import NotifyContainer from "./commons/NotifyContainer";
import emailjs from 'emailjs-com';
import openNotificationWithIcon from "../../../components/Notifictaion";
import OffersSection from "./commons/OffersSection";


const planOptions = [
  { value: "free", name: "Free" },
  { value: "silver", name: "Silver" },
  { value: "gold", name: "Gold" },
  { value: "platinum", name: "Platinum" },
];

const YOUR_SERVICE_ID ='service_4yrz4me';
const YOUR_TEMPLATE_ID='template_6btz26z';
const YOUR_USER_ID='user_AJTlg97b9tVao0VIZOqsY';

//vatidation schema
const MarketingSchema = Yup.object().shape({
  email: Yup.string()
    .email("Invalid email")
    .required(validationMessage.required)
    .matches(patterns.regexValidEmail, validationMessage.email),
});

const MarketingOtherSchema = Yup.object().shape({
  company_name: Yup.string()
    .required(validationMessage.required)
    .max(MAX_LENGHT_30, validationMessage.maxlength30)
    .min(MIN_LENGHT_6, validationMessage.minlength6),

});

//Api
const USER_POST_API = `https://salesapi.bizconnect.com`;

export default function Prelaunch() {
  //state
  const [switch2Date, setSwitch2Date] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [para, setPara] = React.useState(
    "Interested? Please submit your information below  and we’ll notify you as soon as we launch !"
  );
  const [subPara, setSubPara] = React.useState("");

  //initial value
  const initialValues = {
    email: "",
    company_name: "",
    website: "",
    phone: "",
  };

 

  //submit function
  const handleSubmit = (values, { setSubmitting, setStatus, setTouched }) => {
    if (!switch2Date) {
      sendEmail(values)
      setSubmitting(false);
      setTouched({}, false);
      setLoading(true)
    } else {
      let formatedPhone = "";
      if (values.phone !== "") {
        formatedPhone = "+" + values.phone.code + " " + values.phone.phone;
      }
      delete values["phone"];
      values.phone_number = formatedPhone;
      setStatus({ error: null });
      setSubmitting(true);
      submitUserInfo(values);
      console.log(values);
    }
  };


  //send email function

  const sendEmail=(email)=>{
    emailjs.send(YOUR_SERVICE_ID, YOUR_TEMPLATE_ID, email, YOUR_USER_ID)
    .then((result) => {
        openNotificationWithIcon('success',result.text,'Email send successfully ! We will email you as soon as we launch Bizconnect')
        console.log(result.text,'email send');
        setSwitch2Date(true);
        setPara(
            "Thank you for your interest. We will email you as soon as we launch Bizconnect, the world’s first global B2B search engine !"
        );
        setSubPara(
            "Want an early-bird head start? Get ahead of your competition now by filling in the form below and we’ll reach out to you as soon as we launch!"
        );
        setLoading(false)
    }, (error) => {
        console.log(error.text,'failed email');
        setSwitch2Date(false);
        openNotificationWithIcon('error',error.text,'Please verify your email')
        setLoading(false)
    });
  }


  //submit api function
  const submitUserInfo = async (values) => {
    const headers = {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    };
    axios.post(`${USER_POST_API}/sales/v1`, values, headers).then(
      (response) => {
        console.log(response, "response");
        window.location.replace("https://www.bizconnect.com/auth/register");
      },
      (error) => {
        console.log(error);
      }
    );
  };

  //form components
  const renderMarketingForm = () => (
    <Form style={{ width: "100%" }}>
      {!switch2Date ? (
        <>
          <Field
            placeholder="Please enter your email"
            component={AntInput}
            size="large"
            type="email"
            name="email"
          />
          <ButtonBase
            style={{ width: "30%", backgroundColor: "#00077B" }}
            block
            htmlType="submit"
            size="large"
            type="primary"
            loading={loading}
          >
            SEND
          </ButtonBase>
        </>
      ) : (
        <>
          <Field
            placeholder="Please enter your business name"
            type="text"
            component={AntInput}
            name="company_name"
            size="large"
          />
          <Field
            placeholder="Please enter your business website (optional)"
            component={AntInput}
            size="large"
            type="url"
            name="website"
          />

          <ConfigProvider locale={en}>
            <Field
              placeholder="Please enter your phone number (optional)"
              component={AntPhone}
              size="large"
              type="tel"
              name="phone"
            />
          </ConfigProvider>
          <ButtonBase
            style={{ width: "30%", backgroundColor: "#00077B" }}
            block
            htmlType="submit"
            size="large"
            type="primary"
          >
            SUBMIT
          </ButtonBase>
        </>
      )}
    </Form>
  );

  return (
    <React.Fragment>
      <Header></Header>
      <section>
        <div className="getStarted-area" id="getStarted">
          <div className="getting-started-bg">
            <div className="header-text">
              <div className="container">
                <div className="row">
                  <div className="left-text col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <div className="getting-started__banner-text">
                      <h1>
                        The world’s first global B2B Search Engine is coming !
                      </h1>
                    </div>
                    <div className="getting-started__banner-text">
                      <h5>
                        Bizconnect launches on <b>June 1, 2021</b>
                      </h5>
                      <CountDown />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <VideoContainer />
      <NotifyContainer />
      <GettingStartedContentHeading />
      <GettingStartedContentList />
     <OffersSection/>
      <Partners/>
      <section className="getting-started__form" >
        <div className="container">
          <div className="form-container" id="form-section">
            <div className="getting-started__form-heading">
              {subPara === "" ? <h1>{para}</h1> : <h3>{para}</h3>}
              <h3>{subPara}</h3>
            </div>
            {/*  */}
          </div>
        </div>
      </section>
      <section className="getting-started__form">
        <div className="container">
          <div className="getting-started__content-cards">
            <div className="row">
              <div className="col-md-9">
                <div className="card__item-form">
                  <Formik
                    initialValues={initialValues}
                    validationSchema={
                      !switch2Date
                        ? () => {
                            return MarketingSchema;
                          }
                        : () => {
                            return MarketingOtherSchema;
                          }
                    }
                    onSubmit={(values, formActions) =>
                      handleSubmit(values, formActions)
                    }
                  >
                    {renderMarketingForm}
                  </Formik>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer></Footer>
    </React.Fragment>
  );
}
