import React, { useContext } from 'react';
import { ImagePreviewer, FilePreviewer } from 'react-file-utils';
import { ChannelContext } from 'stream-chat-react';
import { PropTypes } from 'prop-types';

export const UploadsPreview = (props) => {
    const {
        fileOrder,
        fileUploads,
        imageOrder,
        imageUploads,
        numberOfUploads,
        removeFile,
        removeImage,
        uploadFile,
        uploadImage,
        uploadNewFiles,
    } = props;
    const channelContext = useContext(ChannelContext);

    return (
        <>
            {imageOrder.length > 0 && (
                <ImagePreviewer
                    imageUploads={imageOrder.map((id) => imageUploads[id])}
                    handleRemove={removeImage}
                    handleRetry={uploadImage}
                    handleFiles={uploadNewFiles}
                    multiple={channelContext.multipleUploads}
                    disabled={
                        channelContext.maxNumberOfFiles !== undefined &&
                        numberOfUploads >= channelContext.maxNumberOfFiles
                    }
                />
            )}
            {fileOrder.length > 0 && (
                <FilePreviewer
                    uploads={fileOrder.map((id) => fileUploads[id])}
                    handleRemove={removeFile}
                    handleRetry={uploadFile}
                    handleFiles={uploadNewFiles}
                />
            )}
        </>
    );
};
UploadsPreview.propTypes = {
    fileOrder: PropTypes.array,
    fileUploads: PropTypes.object,
    imageOrder: PropTypes.array,
    imageUploads: PropTypes.object,
    removeFile: PropTypes.func,
    removeImage: PropTypes.func,
    uploadFile: PropTypes.func,
    uploadImage: PropTypes.func,
    uploadNewFiles: PropTypes.func,
    numberOfUploads: PropTypes.number

};

UploadsPreview.defaultProps = {
    fileOrder: [],
    fileUploads: {},
    imageOrder: [],
    imageUploads: {},
    removeFile: () => { },
    removeImage: () => { },
    uploadFile: () => { },
    uploadImage: () => { },
    uploadNewFiles: () => { },
    numberOfUploads: 0
};
export default UploadsPreview;
