import React, { useState } from "react";
import { IconAdd } from "../../assets/svgs";
import {
    AddNoteWrapperStyled,
    FormInputWrapperStyled,
    ToggleButtonStyled,
    ButtonSubmitWrapperStyled,
    ErrorMessageWrapperStyled,
} from "./AddNote.styles";
//import { Form, Input, Button, Select } from 'antd';
import { notification } from 'antd';
import { Field, Formik, Form } from "formik";
import { object, string } from "yup";
import { AntInput, AntTextArea } from "../../components/Basic/AntFields";
import algoliasearch from 'algoliasearch';

import { CustomerHeader } from './components/CustomerHeader/CustomerHeader'


import {
    validationMessage,
} from "../../common/validates";


const LoginSchema = object({
    catalog: string()
        .required(validationMessage.required),
    schema: string()
        .required(validationMessage.required),
    header: string()
        .required(validationMessage.required),
    des: string()
        .required(validationMessage.required),
});

export const AddNote = () => {
    const [open, setOpen] = useState(false);

    const toggleChatBox = () => {
        setOpen(!open);
    };

    const client = algoliasearch('WLGAEUNVUN', 'b03c378d6e1fbbcb9e26865ce67143b1');
    const index = client.initIndex('instant_search');

    const initialValues = {
        catalog: "",
        schema: "",
        header: "",
        des: "",
        technicalname: "",
        option: "",
        ref: ""
    };

    const [errorMessage, setErrorMessage] = useState("");
    const handleCloseErrorMessage = () => setErrorMessage("");
    const[notify, setNotify] = useState('');

    const openNotification = placement => {
        notification['success']({
            message: `Notification add record`,
            description:
                `${notify} was added to search engine...`,
            placement,
        });

        // setTimeout(()=>{
        //     window.location.reload();
        // }, 1000);
    };

    const renderInputForm = ({ isSubmitting, isValid, errors, touched }) => (
        <Form>
            {errorMessage && (
                <ErrorMessageWrapperStyled
                    message={errorMessage}
                    type="error"
                    closable
                    onClose={handleCloseErrorMessage}
                />
            )}
            <Field
                placeholder="Please enter Catalog"
                type="text"
                component={AntInput}
                name="catalog"
                size="large"
                label="Catalog"
                maxLength={1000}
                disabled={isSubmitting}
            />
            <Field
                placeholder="Please enter Schema"
                type="text"
                component={AntInput}
                name="schema"
                size="large"
                label="Schema"
                maxLength={1000}
                disabled={isSubmitting}
            />
            <Field
                placeholder="Please enter header"
                type="text"
                component={AntInput}
                name="header"
                size="large"
                label="Header"
                maxLength={1000}
                disabled={isSubmitting}
            />
            <Field
                placeholder="Please enter description"
                type="text"
                component={AntTextArea}
                name="des"
                size="large"
                label="Description"
                maxLength={1000}
                disabled={isSubmitting}
            />
            <Field
                placeholder="Please enter technical name"
                type="text"
                component={AntInput}
                name="technicalname"
                size="large"
                label="technical name"
                maxLength={1000}
                disabled={isSubmitting}
            />
            <Field
                placeholder="Please enter Option field"
                type="text"
                component={AntInput}
                name="option"
                size="large"
                label="Option field"
                maxLength={1000}
                disabled={isSubmitting}
            />
            <Field
                placeholder="Please enter reference link"
                type="text"
                component={AntInput}
                name="ref"
                size="large"
                label="reference link"
                maxLength={1000}
                disabled={isSubmitting}
            />
            <ButtonSubmitWrapperStyled
                block
                htmlType="submit"
                size="large"
                disabled={isSubmitting || !isValid}
                loading={isSubmitting}
            >
                Submit
            </ButtonSubmitWrapperStyled>
        </Form>
    );

    const handleSubmit = async (values, { setSubmitting, setStatus }) => {
        setStatus({ error: null });
        setSubmitting(true);

        const records =
        {
            catalog: values.catalog,
            schema: values.schema,
            header: values.header,
            des: values.des,
            technical: values.technicalname ? values.technicalname : null,
            option: values.option ? values.technicalname : null,
            ref: values.ref ? values.ref : null,
        };

        // index
        //     .saveObject(records, { autoGenerateObjectIDIfNotExist: true })
        //     .then(({ objectIDs }) => {
        //         console.log(objectIDs);
        //     });

        setNotify(values.header);
        openNotification('topRight');
    };

    return (
        <AddNoteWrapperStyled>
            {open && (
                <>
                    <CustomerHeader onClose={toggleChatBox} />
                    <FormInputWrapperStyled>
                        <Formik
                            initialValues={initialValues}
                            validationSchema={LoginSchema}
                            onSubmit={(values, formActions) => handleSubmit(values, formActions)
                                .then(() => {
                                    formActions.resetForm({
                                        values: {
                                            catalog: "",
                                            schema: "",
                                            header: "",
                                            des: "",
                                            technicalname: "",
                                            option: "",
                                            ref: ""
                                        }
                                    })
                                })}

                        >
                            {renderInputForm}
                        </Formik>
                    </FormInputWrapperStyled>
                </>
            )}
            <ToggleButtonStyled onClick={toggleChatBox}>{!open && <IconAdd />}</ToggleButtonStyled>
        </AddNoteWrapperStyled>
    );
};

export default AddNote;
