const IconStyle = styled.i`
    color:#5468FF;
`
const ButtonAddStyle = styled(Button)`
  margin-top:5px;
  color: #5468FF;
  padding: 0;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.3), inset 0 1px 0 0 rgba(255, 255, 255, 0.18);
`

const InputStyle = styled.input`
  padding-left: 30px;
`

const App = props => {
  return (
    <div>
      <InstantSearch
        searchClient={searchClient}
        indexName="instant_search"
        searchState={props.searchState}
        createURL={props.createURL}
        onSearchStateChange={props.onSearchStateChange}
      >
        <Configure hitsPerPage={10} />
        <Header />
        <section>
          <Facets />
          <Results />
        </section>
      </InstantSearch>
    </div>
  );
}

const Header = () => {
  const [visibleAdd, setVisibleAdd] = useState(false);
  const [initialValuesAdd, setInitialValuesAdd] = useState({
    catalog: "",
    schema: "",
    header: "",
    des: "",
    technicalname: "",
    option: "",
    ref: ""
  });
  const onOpenAdd = () => {
    setVisibleAdd(true);
    const initialValues = {
      catalog: '',
      schema: '',
      header: '',
      des: '',
      technicalname: '',
      ref: ''
    };

    setInitialValuesAdd(initialValues);
  }

  const onCloseAdd = () => {
    setVisibleAdd(false);
  }

  return (
    <PageHeaderStyle
      title={
        <Space direction="vertical" size="1">
          <Link to="/">
            <img className="logo" src={logo} alt="logo" />
          </Link>
        </Space>
      }
      subTitle={
        <SearchBox />
      }
      extra={
        <ExtraStyle>
          <AddNewRecord visible={visibleAdd} handleClose={onCloseAdd} hit={initialValuesAdd} />
          <Tooltip placement="leftBottom" title='Add Record'>
            <ButtonAddStyle onClick={onOpenAdd}><i class="fa fa-plus-circle fa-2x"></i></ButtonAddStyle>
          </Tooltip>
        </ExtraStyle>
      }
    />);
};

const SearchBox = connectSearchBox(({ currentRefinement, refine }) => (
  <div className="searchbox-container">
    <div className="input-group">
      <span className="input-group-btn">
        <button className="btn btn-default">
          <i className="fa fa-search" />
        </button>
      </span>
      <InputStyle
        type="text"
        placeholder="Search"
        value={currentRefinement}
        onChange={e => refine(e.target.value)}
        autoComplete="off"
        className="form-control"
      />
    </div>
  </div>
));

const Facets = () => (
  <aside>
    <ul className="nav nav-list panel">
      <li>
        <a href="./">
          <i className="fa fa-home" /> Home
        </a>
      </li>
      <li className="separator" />
    </ul>
    <Panel title="Catalog" id="genres">
      <RefinementListLinks attribute="catalog" />
    </Panel>
    <Panel title="Schema" id="genres">
      <RefinementListLinks attribute="schema" />
    </Panel>
    {/* <Panel title="Rating" id="ratings">
      <RatingMenu attribute="rating" max={5} />
    </Panel> */}
    <div className="thank-you">
      {' '}
      Data courtesy of <a href="https://www.algolia.com/apps/WLGAEUNVUN/explorer/ui-demos/instant_search">Tung Tran</a>
    </div>
  </aside>
);

const Panel = ({ title, children, id }) => (
  <div id={id}>
    <h5>
      <i className="fa fa-chevron-right" /> {title}
    </h5>
    {children}
  </div>
);

const Genre = ({ name }) => <span className="badge">{name}</span>;
const Genres = ({ genres }) => (
  <p className="genre">
    {genres.map((genre, idx) => (
      <Genre name={genre} key={idx} />
    ))}
  </p>
);

const convertWCFStringDate = (strDate) => {
  return <Moment format="YYYY/MM/DD">{strDate}</Moment>
}

const Hit = hit => {
  const { objectID, catalog, schema, header, des, technical, ref, creationDate, updateDate } = hit.hit;

  const [visible, setVisible] = useState(false);
  const [initialValues, setInitialValues] = useState({
    objectID: "",
    catalog: "",
    schema: "",
    header: "",
    des: "",
    technicalname: "",
    ref: "",
    creationDate: "",
    updateDate: ""
  });

  const onOpen = () => {
    setVisible(true);
    const initialValues = {
      objectID: objectID,
      catalog: catalog,
      schema: schema,
      header: header,
      des: des,
      technicalname: technical,
      ref: ref,
      creationDate: creationDate,
      updateDate: updateDate
    };

    setInitialValues(initialValues);
  }

  const onClose = () => {
    setVisible(false);
  }

  return (
    <>
      {visible && <EditSearch visible={visible} handleClose={onClose} hit={initialValues} />}
      <ListItem>
        <ContainerStyles>
          <WrapperItem>
            <ListItemHeader direction="vertical" size="small" style={{ width: '100%' }}>
              <Space size="large" style={{ width: '100%', justifyContent: 'space-between' }}>
                <Space size="large">
                  <Title level={4} style={{ marginBottom: 0 }}>
                    <a className="primaryAttributeStyles" href={ref} target="_blank">
                      <Highlight attribute="header" hit={hit.hit} />
                    </a>
                  </Title>
                </Space>
              </Space>
            </ListItemHeader>

            <ListItemContent>
              <Space direction="vertical">
                <Text className="secondAttributeStyles">{des}</Text>
                <Text className="secondAttributeStyles">
                  <strong>catalog: </strong>{catalog}
                  <strong><span className="tab"></span>schema: </strong>{schema}
                </Text>
                {technical ? <Text className="secondAttributeStyles"><strong>technical:</strong>{technical}</Text> : null}
                <Text className="dateStyles">
                  <strong>Created date: </strong>{convertWCFStringDate(creationDate)}
                  <span className="tab"></span>
                  <strong>Updated date: </strong>{convertWCFStringDate(updateDate)}
                </Text>
              </Space>
            </ListItemContent>
          </WrapperItem>

          <RightItemStyle>
            <Tooltip placement="leftBottom" title='Edit Record'>
              <ButtonAddStyle>
                <IconStyle onClick={onOpen}><i class="fa fa-edit fa-lg"></i></IconStyle>
              </ButtonAddStyle>
            </Tooltip>

          </RightItemStyle>
        </ContainerStyles>
      </ListItem>
    </>


  );
};


const Results = connectSearchBox(() => (
  <article>
    <div id="stats" className="text-right text-muted textResult">
      <Stats />
    </div>
    <hr />
    <div id="hits">
      <Hits hitComponent={Hit} />
    </div>
    <div id="pagination" className="text-center">
      <Pagination />
    </div>
  </article>
));

const RefinementListLinks = connectRefinementList(
  ({ items, refine, createURL }) => {
    const hitComponents = items.map(item => (
      <div className={item.isRefined ? ' active' : ''} key={item.label}>
        <a
          className="item"
          href={createURL(item.value)}
          onClick={e => {
            e.preventDefault();
            refine(item.value);
          }}
        >
          <span> {item.label}</span>
          <span className="badge pull-right">{item.count}</span>
        </a>
      </div>
    ));

    return <div className="nav nav-list">{hitComponents}</div>;
  }
);

export default withURLSync(App);