import React, { useState } from "react";
import { StreamChat } from "stream-chat";
import { MessageCommerce, MessageList } from "stream-chat-react";

import GuestUserInput from "./components/GuestUserInput/GuestUserInput";
import { Chat, Channel, Window, MessageInput } from "stream-chat-react";
import CustomerHeader from "./components/CustomerHeader/CustomerHeader";
import CustomerMessageInput from "./components/CustomerMessageInput/CustomerMessageInput";
import EmptyStateIndicator from "./components/EmptyStateIndicator/EmptyStateIndicator";
import { IconOpenChat } from "../../assets/svgs";
import streamConfig from "../../config/streamConfig";
import { CustomerAppWrapperStyled, ToggleButtonStyled } from "./ChatWidget.styles";

const chatClient = new StreamChat(streamConfig.streamAppKey);

export const ChatWidget = () => {
  const [open, setOpen] = useState(false);
  const [name, setName] = useState();
  const [channel, setChannel] = React.useState(null);

  const toggleChatBox = () => {
    setOpen(!open);
  };

  return (
    <Chat client={chatClient} theme={`${streamConfig.streamChannel} light`}>
      <CustomerAppWrapperStyled>
        {open && (
          <>
            {channel ? (
              <Channel channel={channel}>
                <Window>
                  <CustomerHeader onClose={toggleChatBox} />
                  <MessageList
                    EmptyStateIndicator={(props) => (
                      <EmptyStateIndicator channel={channel} name={name} />
                    )}
                    Message={MessageCommerce}
                  />
                  <MessageInput
                    Input={() => <CustomerMessageInput open={open} setOpen={setOpen} />}
                    focus
                  />
                </Window>
              </Channel>
            ) : (
              <Window>
                <CustomerHeader onClose={toggleChatBox} />
                <GuestUserInput chatClient={chatClient} setChannel={setChannel} setName={setName} />
              </Window>
            )}
          </>
        )}
        <ToggleButtonStyled onClick={toggleChatBox}>{!open && <IconOpenChat />}</ToggleButtonStyled>
      </CustomerAppWrapperStyled>
    </Chat>
  );
};

export default ChatWidget;
