import React from "react";
import {
  AntDatePicker,
  AntInput,
  AntPhone,
  AntSelect,
} from "../../../components/Basic/AntFields";
import { Field, Formik, Form } from "formik";
import ButtonBase from "../../../components/button";
import { Link } from "react-scroll";
import {
  validationMessage,
  MAX_LENGHT_30,
  MIN_LENGHT_6,
  patterns,
} from "../../../common/validates";
import * as Yup from "yup";
import moment from "moment";
import { ConfigProvider } from "antd-country-phone-input";
import en from "world_countries_lists/data/en/world.json";
import Partners from "../../../components/Partners";
import axios from 'axios'
import Footer from "../../commons/Footer";
import Header from "../../commons/Header";
import VideoContainer from "./commons/VideoContainer";
import GettingStartedContentHeading from "./commons/GettingStartedContentHeading";
import GettingStartedContentList from "./commons/GettingStartedContentList";
import PackageContainer from "./commons/PackageContainer";
import NotifyContainer from "./commons/NotifyContainer";
import OffersSection from "./commons/OffersSection";

const planOptions = [
  { value: "free", name: "Free" },
  { value: "silver", name: "Silver" },
  { value: "gold", name: "Gold" },
  { value: "platinum", name: "Platinum" },
];




//vatidation schema
const MarketingSchema = Yup.object().shape({
  name: Yup.string()
    .required(validationMessage.required)
    .max(MAX_LENGHT_30, validationMessage.maxlength30)
    .min(MIN_LENGHT_6, validationMessage.minlength6),

  email: Yup.string()
    .email("Invalid email")
    .required(validationMessage.required)
    .matches(patterns.regexValidEmail, validationMessage.email)
    .matches(patterns.regexEmail),
});

const MarketingOtherSchema = Yup.object().shape({
  company_name: Yup.string()
    .required(validationMessage.required)
    .max(MAX_LENGHT_30, validationMessage.maxlength30)
    .min(MIN_LENGHT_6, validationMessage.minlength6),

  plan: Yup.string().required(validationMessage.required).nullable(),

  checkTime: Yup.string().required(validationMessage.required),
});



//Api
const USER_POST_API = `https://salesapi.bizconnect.com`;


export default function GetStarted() {



  //state
  const [switch2Date, setSwitch2Date] = React.useState(false);
  const [formatedDate, setFormatedDate] = React.useState("");
  const [selectedPlan, setSelectedPlan] = React.useState("");
  const [para, setPara] = React.useState(
    "Interested? Please submit your information below and we’ll get you started:"
  );


  //initial value
  const initialValues = {
    name: "",
    email: "",
    company_name: "",
    website: "",
    phone: "",
    checkTime: "",
    plan: selectedPlan !== "" ? selectedPlan : null,
  };



  //function to disable future date
  const disabledDate = (current) => {
    return current && current < moment().endOf("day");
  };



  //submit function
  const handleSubmit = (values, { setSubmitting, setStatus, setTouched }) => {
    if (!switch2Date) {
      setSwitch2Date(true);
      setSubmitting(false);
      setTouched({}, false);
      setPara(
        "Just one more step and you’ll be well on your way to becoming featured on our brand new Global B2B Search Engine!"
      );
    } else {
      let formatedPhone = "";
      if (values.phone !== "") {
        formatedPhone = "+" + values.phone.code + " " + values.phone.phone;
      }
      delete values["checkTime"];
      delete values["phone"];
      values.contact_time = formatedDate;
      values.phone_number = formatedPhone;
      setStatus({ error: null });
      setSubmitting(true);
      submitUserInfo(values)      
    }
  };

const submitUserInfo = async (values) => {
    const headers =   {
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*"
    }
    axios.post(`${USER_POST_API}/sales/v1`,values,
      headers
    )
      .then(
        (response) => {
          console.log(response,'response')
          window.location.replace("https://www.bizconnect.com/auth/register");
        },
        (error) => {
          console.log(error)
        }
      )
  }


  //form components
  const renderMarketingForm = ({ isSubmitting, isValid, touched }) => (
    <Form style={{ width: "100%" }}>
      {!switch2Date ? (
        <>
          <Field
            placeholder="Please enter your name"
            type="text"
            component={AntInput}
            name="name"
            size="large"
            selectedPlan={selectedPlan}
            setSelectedPlan={setSelectedPlan}
          />
          <Field
            placeholder="Please enter your email"
            component={AntInput}
            size="large"
            type="email"
            name="email"
          />
          <ButtonBase
            style={{ width: "30%", backgroundColor: "#00077B" }}
            block
            htmlType="submit"
            size="large"
            type="primary"
          >
            NEXT
          </ButtonBase>
        </>
      ) : (
        <>
          <Field
            placeholder="Please enter your business name"
            type="text"
            component={AntInput}
            name="company_name"
            size="large"
          />
          <Field
            placeholder="Please enter your business website (optional)"
            component={AntInput}
            size="large"
            type="url"
            name="website"
          />

          <ConfigProvider locale={en}>
            <Field
              placeholder="Please enter your phone number (optional)"
              component={AntPhone}
              size="large"
              type="tel"
              name="phone"
            />
          </ConfigProvider>

          <Field
            size="large"
            name="plan"
            component={AntSelect}
            selectOptions={planOptions}
            type="select"
            placeholder="Select a Plan: (Free, Silver, Gold, Platinum)"
          />
          <Field
            placeholder="Best time for us to reach you"
            component={AntDatePicker}
            showTime={{ format: "HH:mm" }}
            format="DD-MM-YYYY HH:mm"
            size="large"
            type="dateTime"
            name="checkTime"
            setFormatedDate={setFormatedDate}
            disabledDate={disabledDate}
            showNow={false}
            allowClear={false}
          />

          <ButtonBase
            style={{ width: "30%", backgroundColor: "#00077B" }}
            block
            htmlType="submit"
            size="large"
            type="primary"
          >
            SUBMIT
          </ButtonBase>
        </>
      )}
    </Form>
  );

  return (
    <React.Fragment>
      <Header/>
      <section>
        <div className="getStarted-area" id="getStarted">
          <div className="getting-started-bg">
            <div className="header-text">
              <div className="container">
                <div className="row">
                  <div className="left-text col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <div className="getting-started__banner-text">
                      <h1>
                        Gain new verified B2B leads and buyers for your business
                        on Bizconnect
                      </h1>
                      <h5>The world’s first global B2B Search Engine!</h5>
                      <Link to="form-section" spy={true} smooth={true}>
                        <a
                          href="javascript:void(0)"
                          className="getting-started__banner-button-slider"
                        >
                          Get Started Now!
                        </a>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <VideoContainer/>
      <NotifyContainer started={'started'}/>
      <GettingStartedContentHeading/>
      <GettingStartedContentList/>
      <OffersSection started={'started'}/>
       <Partners></Partners>
       <PackageContainer setSelectedPlan={setSelectedPlan}/>
       <section className="getting-started__form" id="form-section">
        <div className="container">
          <div className="form-container">
            <div className="getting-started__form-heading">
              <h1>{para}</h1>
            </div>
            {/*  */}
          </div>
        </div>
      </section>
      <section className="getting-started__form">
        <div className="container">
          <div className="getting-started__content-cards">
            <div className="row">
              <div className="col-md-9">
                <div className="card__item-form">
                  <Formik
                    initialValues={initialValues}
                    validationSchema={
                      !switch2Date
                        ? () => {
                            return MarketingSchema;
                          }
                        : () => {
                            return MarketingOtherSchema;
                          }
                    }
                    onSubmit={(values, formActions) =>
                      handleSubmit(values, formActions)
                    }
                  >
                    {renderMarketingForm}
                  </Formik>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Footer/>
    </React.Fragment>
  );
}
