import moment from 'moment';

export const dateFormat = 'MM-DD-YYYY';
const requiredMessage = () => 'This is a required field.';
const maxLengthMessage = length => `Maximum ${length} characters.`;
const minLengthMessage = length => `Mininum ${length} characters.`;
const comparePassword = () => "Password confirmation doesn't match Password.";
const passwordMessage = () =>
    'Password must be between 8 and 16 characters in length,\
 at least one uppercase character, at least one number, least one special characters(eg. !@#$%^&* and must not contain spaces.';
const emailMessage = () => 'Invalid email address.';
const notAllowEmailMessage = () => 'Personal email addresses are not allowed.';

export const MIN_LENGHT_2 = 2;
export const MIN_LENGHT_6 = 6;
export const LENGHT_10 = 10;
export const MAX_LENGHT_30 = 30;
export const MAX_LENGHT_50 = 50;
export const MAX_LENGHT_100 = 100;
export const LENGHT_160 = 160;
export const MAX_LENGHT_256 = 256;
export const LENGHT_7 = 7;
export const LENGHT_15 = 15;
export const LENGHT_3 = 3;
export const LENGHT_320 = 320;
export const LENGHT_64 = 64;
export const LENGHT_255 = 255;
export const LENGTH_35 = 35;
export const LENGTH_5 = 5;

export const patterns = {
    // Regex for password must contain at least eight characters,
    // at least one number and both lower and uppercase letters and special characters
    regexPassword: /^(?!.* )(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,16}$/,
    regexEmail: /^([\w-.]+@(?!gmail\.com)(?!yahoo\.com)(?!hotmail\.com)(?!mail\.com)([\w-]+.)+[\w-]{2,4})?$/,
    regexValidEmail: /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,4})+$/
};

export const validationMessage = {
    required: requiredMessage(),
    maxLength256: maxLengthMessage(MAX_LENGHT_256),
    maxLength160: maxLengthMessage(LENGHT_160),
    maxLength100: maxLengthMessage(MAX_LENGHT_100),
    maxLength10: maxLengthMessage(LENGHT_10),
    minlength50: minLengthMessage(MAX_LENGHT_50),
    maxlength30: maxLengthMessage(MAX_LENGHT_30),
    minlength2: minLengthMessage(MIN_LENGHT_2),
    minlength6: minLengthMessage(MIN_LENGHT_6),
    comparePassword: comparePassword(),
    password: passwordMessage(),
    email: emailMessage(),
    notAllowEmail: notAllowEmailMessage(),
    minlength7: minLengthMessage(LENGHT_7),
    maxlength15: maxLengthMessage(LENGHT_15),
    maxlength320: maxLengthMessage(LENGHT_320),
    maxlength64: maxLengthMessage(LENGHT_64),
    maxlength255: maxLengthMessage(LENGHT_255),
    minlength3: minLengthMessage(LENGHT_3),
    minlength5: minLengthMessage(LENGTH_5),
    maxlength35: maxLengthMessage(LENGTH_35)
};

export const validateDate = value => {
    let errors;

    if (!value) {
        errors = requiredMessage();
    } else if (moment(value).format(dateFormat) < moment(Date.now()).format(dateFormat)) {
        errors = 'Invalid date!';
    }

    return errors;
};

export const validateEmail = value => {
    let errors;
    const regex = /^[\w-\\.]+@([yahoo+\\.]|[gmail+\\.])+[\w-]{2,4}$/;
    const regexValidEmail = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;
    if (!value || value.trim().length === 0) {
        errors = requiredMessage();
    } else if (regex.test(value) || !regexValidEmail.test(value)) {
        errors = 'Invalid email address!';
    }
    return errors;
};

export const validatePassword = value => {
    let errors;
    // Regex for password must be between 8 and 16 characters in length.,
    // at least one number and at least one uppercase letters
    const regularExpression = /^(?=.*?[A-Z])(?=.*?[0-9]).{8,16}$/;
    if (!value) {
        errors = requiredMessage();
    } else if (!regularExpression.test(value)) {
        errors = 'Invalid Password!';
    }
    return errors;
};

export const validateConfirmPassword = (form, value) => {
    let errors;
    if (!value) {
        errors = requiredMessage();
    } else if (form.current.values.password !== value) {
        errors = "Password confirmation doesn't match Password";
    }
    return errors;
};

export const isRequired = value => (!value || value.trim().length === 0 ? requiredMessage() : '');
