import React, { useCallback } from "react";
import { scroller } from "react-scroll";
import tick from "./assests/tick.svg";
import cross from "./assests/cross.svg";
import "./assests/style.css"
export default function PaymentCard({ data, setSelectedPlan }) {


  //function to scroll down to bottom and make card active

  const handleClick = useCallback((id) => {
    scroller.scrollTo("form-section", {
      duration: 800,
      delay: 0,
      smooth: "easeInOutQuart",
    });
    document
      .querySelector(".card__Pricing-item.active")
      .classList.remove("active");
    document.getElementById(id).classList.add("active");
    setSelectedPlan(id);
  }, []);




  return (
    <div
      className={
        data?.id === "platinum"
          ? "card__Pricing-item active"
          : "card__Pricing-item"
      }
      id={data?.id}
    >
      <div className="card__container">
        <div className="card__title">
          <h2 className="card__heading">{data?.title}</h2>
          <p>{data?.heading}</p>
        </div>
        <div className="card__Pricing-container">
          <p>
            <span className="price-tag">${data?.price}</span>
          </p>
          <small>One-time Payment</small>
          <p><b>{data?.freecredits}</b></p>
          <div className="Price__button-container">
            <button type="button" onClick={() => handleClick(data?.id)}>
              Get Started
            </button>
          </div>
          <div className="price__feature">
            <ul>
              {data.body.map((item, i) => (
                <li className="list-item" key={i}>
                  <figure>
                    <img src={item?.available ? tick : cross} />
                  </figure>
                  <p>
                    {item?.point?.name}{" "}
                    {item?.point?.featured ? (
                      <b> - {item?.point?.featured}</b>
                    ) : (
                      ""
                    )}
                  </p>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}
