import React, { useState } from "react";
import PropTypes from "prop-types";
import { v4 as uuidv4 } from "uuid";
import { object, string } from "yup";

import { Field, Formik, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import { AntInput } from "../../../../components/Basic/AntFields";
import streamConfig from "../../../../config/streamConfig";
import {
  ErrorMessageWrapperStyled,
  ChatContainerListWrapperStyled,
  CheckBoxWrapperStyled,
  ButtonStartChatWrapperStyled,
} from "./GuestuserInput.styles";

import {
  validationMessage,
  MAX_LENGHT_256,
  MAX_LENGHT_100,
  MIN_LENGHT_6,
  patterns,
} from "../../../../common/validates";

// const chatClient = new StreamChat(streamConfig.streamAppKey);

const LoginSchema = object({
  email: string()
    .required(validationMessage.required)
    .max(MAX_LENGHT_256, validationMessage.maxLength256)
    .min(MIN_LENGHT_6, validationMessage.minlength6)
    .matches(patterns.regexValidEmail, validationMessage.email),

  name: string()
    .required(validationMessage.required)
    .max(MAX_LENGHT_100, validationMessage.maxlength100)
    .min(MIN_LENGHT_6, validationMessage.minlength6),

  acceptTerms: Yup.bool().oneOf([true], "Accept Terms & Conditions is required"),
});

const GuestUserInput = (props) => {
  const { chatClient, setChannel, setName } = props;

  const initialValues = {
    name: "",
    email: "",
    acceptTerms: false,
  };

  const [errorMessage, setErrorMessage] = useState("");
  const handleCloseErrorMessage = () => setErrorMessage("");

  const renderInputForm = ({ isSubmitting, isValid, errors, touched }) => (
    <Form>
      {errorMessage && (
        <ErrorMessageWrapperStyled
          message={errorMessage}
          type="error"
          closable
          onClose={handleCloseErrorMessage}
        />
      )}
      <Field
        placeholder="Please enter your name"
        type="text"
        component={AntInput}
        name="name"
        size="large"
        label="Your Name:"
        maxLength={1000}
        disabled={isSubmitting}
      />
      <Field
        placeholder="Please enter your email"
        type="text"
        component={AntInput}
        name="email"
        size="large"
        label="E-mail:"
        maxLength={1000}
        disabled={isSubmitting}
      />
      <CheckBoxWrapperStyled className="form-group form-check">
        <Field
          type="checkbox"
          name="acceptTerms"
          className={
            "form-check-input " + (errors.acceptTerms && touched.acceptTerms ? " is-invalid" : "")
          }
        />
        <label htmlFor="acceptTerms" className="form-check-label">
          I agree to have my personal data processed by <strong>BizConnect </strong>for chat support.
        </label>
        <ErrorMessage name="acceptTerms" component="div" className="invalid-feedback" />
      </CheckBoxWrapperStyled>
      <ButtonStartChatWrapperStyled
        block
        htmlType="submit"
        size="large"
        disabled={isSubmitting || !isValid}
        loading={isSubmitting}
      >
        Start chat
      </ButtonStartChatWrapperStyled>
    </Form>
  );

  const handleSubmit = async (values, { setSubmitting, setStatus }) => {
    setStatus({ error: null });
    setSubmitting(true);

    const userId = `guest-${uuidv4()}`;
    const assignedSupportAgent = streamConfig.streamSupportAgentId;

    await chatClient.setGuestUser({
      id: userId,
      name: values.name,
      email: values.email,
    });

    console.log("chatClient: ", chatClient);

    const channel = await chatClient.channel(streamConfig.streamChannel, userId, {
      assigned: assignedSupportAgent,
      issue: "Guest Support",
      subtitle: "Guest Support", // Generate ticket number here
      status: "open",
      members: [chatClient.user.id, assignedSupportAgent],
    });

    await channel.watch({ presence: true });

    setChannel(channel);
    setName(values.name);
  };

  return (
    <ChatContainerListWrapperStyled>
      <Formik
        initialValues={initialValues}
        validationSchema={LoginSchema}
        onSubmit={(values, formActions) => handleSubmit(values, formActions)}
      >
        {renderInputForm}
      </Formik>
    </ChatContainerListWrapperStyled>
  );
};

GuestUserInput.propTypes = {
  chatClient: PropTypes.any.isRequired,
  setChannel: PropTypes.func.isRequired,
  setName: PropTypes.func.isRequired,
};

export default GuestUserInput;
