import React, { useContext, useEffect } from "react";
import PropTypes from "prop-types";
import { ImageDropzone, FileUploadButton } from "react-file-utils";
import { ChannelContext, ChatAutoComplete, EmojiPicker, useMessageInput } from "stream-chat-react";
import { UploadsPreview } from "./UploadsPreview";
import { FileIcon, SmileyFace } from "../../../../assets/svgs";

import {
  SupportMessageInputWrapperStyled,
  SupportMessageInputInputStyled,
  SupportMessageInputInputWrapperStyled,
  SupportMessageInputIconWrapperStyled,
} from "./CustomerMessageInput.styles";

const CustomerMessageInput = (props) => {
  const { open, setOpen } = props;

  const messageInput = useMessageInput(props);

  const { acceptedFiles, maxNumberOfFiles, multipleUploads } = useContext(ChannelContext);

  useEffect(() => {
    if (open) {
      messageInput.textareaRef.current.focus();
    } else {
      messageInput.textareaRef.current.blur();
    }
  }, [messageInput.textareaRef, open]);

  useEffect(() => {
    if (messageInput.text) {
      setOpen(true);
    }
  }, [messageInput.text, setOpen]);

  const handleSubmit = (e) => {
    if (messageInput.text.startsWith("/")) {
      e.target.value = "";
      return messageInput.handleChange(e);
    }
    messageInput.handleSubmit(e);
  };

  return (
    <SupportMessageInputWrapperStyled>
      <ImageDropzone
        accept={acceptedFiles}
        handleFiles={messageInput.uploadNewFiles}
        multiple={multipleUploads}
        disabled={
          maxNumberOfFiles !== undefined && messageInput.numberOfUploads >= maxNumberOfFiles
        }
      >
        <SupportMessageInputInputStyled>
          <UploadsPreview {...messageInput} />
          <SupportMessageInputInputWrapperStyled>
            <ChatAutoComplete
              commands={messageInput.getCommands()}
              innerRef={messageInput.textareaRef}
              handleSubmit={handleSubmit}
              onSelectItem={messageInput.onSelectItem}
              onChange={messageInput.handleChange}
              value={messageInput.text}
              rows={1}
              placeholder="Ask us a question"
              onPaste={messageInput.onPaste}
            />
            <SupportMessageInputIconWrapperStyled>
              <SmileyFace openEmojiPicker={messageInput.openEmojiPicker} />
              <FileUploadButton handleFiles={messageInput.uploadNewFiles}>
                <FileIcon />
              </FileUploadButton>
            </SupportMessageInputIconWrapperStyled>
          </SupportMessageInputInputWrapperStyled>
        </SupportMessageInputInputStyled>
      </ImageDropzone>
      <EmojiPicker {...messageInput} />
    </SupportMessageInputWrapperStyled>
  );
};

CustomerMessageInput.propTypes = {
  open: PropTypes.bool,
  setOpen: PropTypes.func.isRequired,
};

CustomerMessageInput.defaultProps = {
  open: false,
};

export default CustomerMessageInput;
