import React, { useEffect, useState } from "react";
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { notification, Alert, Button, Modal, Space, Typography } from 'antd';
import { Field, Formik, Form } from "formik";
import { object, string } from "yup";
import { AntInput, AntTextArea } from "../../../../components/Basic/AntFields";
import algoliasearch from 'algoliasearch';

import {
    validationMessage,
} from "../../../../common/validates";
import { boolean } from "yup/lib/locale";

const { Title } = Typography;

export const ErrorMessageWrapperStyled = styled(Alert)`
    margin-bottom: 10px;

    .ant-alert-message {
        color: red;
    }
`;

export const ButtonSubmitWrapperStyled = styled(Button)`
    font-size: 16px;
    border-radius: 8px;
    background-color: #5468FF;
    border-color: #5468FF;
    color: #ffffff;
    justify-content: center;
    align-items: center;
`;

export const FormInputWrapperStyled = styled.div`
    .ant-form-item {
        margin-bottom: 5px;
    }
`

const LoginSchema = object({
    catalog: string()
        .required(validationMessage.required),
    schema: string()
        .required(validationMessage.required),
    header: string()
        .required(validationMessage.required),
    des: string()
        .required(validationMessage.required),
});

export const AddNewRecord = (props) => {
    const { visible, handleClose } = props;

    const client = algoliasearch('WLGAEUNVUN', 'b03c378d6e1fbbcb9e26865ce67143b1');
    const index = client.initIndex('instant_search');

    const initialValues = {
        catalog: '',
        schema: '',
        header: '',
        des: '',
        technicalname: '',
        ref: '',
        creationDate: '',
        updateDate:'',
    };

    console.log('initialValues:', initialValues)

    const [errorMessage, setErrorMessage] = useState("");
    const handleCloseErrorMessage = () => setErrorMessage("");
    const [notify, setNotify] = useState('');

    const openNotification = placement => {
        notification['success']({
            message: `Notification add record`,
            description:
                `${notify} was added to search engine...`,
            placement,
        });
    };

    const handleSubmit = async (values, { setSubmitting, setStatus }) => {
        setStatus({ error: null });
        setSubmitting(true);

        let date = new Date();

        const records =
        {
            catalog: values.catalog,
            schema: values.schema,
            header: values.header,
            des: values.des,
            technical: values.technicalname ? values.technicalname : null,
            ref: values.ref ? values.ref : null,
            creationDate: date,
            updateDate: date
        };

        console.log('creationDate: ', records.creationDate)
        index.saveObject(records, { autoGenerateObjectIDIfNotExist: true })
            .then(({ objectIDs }) => {
                console.log(objectIDs);
            });

        setNotify(records.header);
    };

    useEffect(() => {
        if (notify.length > 0 ) {
            openNotification('topRight');
        }
    }, [notify]);

    return (
        <Formik
            initialValues={initialValues}
            validationSchema={LoginSchema}
            onSubmit={(values, formActions) => handleSubmit(values, formActions).then(() => {
                formActions.resetForm({
                    values: {
                        catalog: "",
                        schema: "",
                        header: "",
                        des: "",
                        technicalname: "",
                        ref: ""
                    }
                })
            })}
        >
            {({ isSubmitting, isValid, resetForm, setFieldValue }) => (
                <Modal
                    centered
                    maskClosable={false}
                    visible={visible}
                    onCancel={() => {
                        resetForm();
                        handleClose();
                    }}
                    width={1000}
                    footer={null}
                >
                    <Space size="large" direction="vertical" style={{ width: ' 100%' }}>
                        <Title level={4}>Update the record</Title>
                        <FormInputWrapperStyled>
                            <Form>
                                {errorMessage && (
                                    <ErrorMessageWrapperStyled
                                        message={errorMessage}
                                        type="error"
                                        closable
                                        onClose={handleCloseErrorMessage}
                                    />
                                )}
                                <Field
                                    placeholder="Please enter Catalog"
                                    type="text"
                                    component={AntInput}
                                    name="catalog"
                                    size="large"
                                    label="Catalog"
                                    maxLength={1000}
                                    disabled={isSubmitting}
                                />
                                <Field
                                    placeholder="Please enter Schema"
                                    type="text"
                                    component={AntInput}
                                    name="schema"
                                    size="large"
                                    label="Schema"
                                    maxLength={1000}
                                    disabled={isSubmitting}
                                />
                                <Field
                                    placeholder="Please enter header"
                                    type="text"
                                    component={AntInput}
                                    name="header"
                                    size="large"
                                    label="Header"
                                    maxLength={1000}
                                    disabled={isSubmitting}
                                />
                                <Field
                                    placeholder="Please enter description"
                                    type="text"
                                    component={AntTextArea}
                                    name="des"
                                    size="large"
                                    label="Description"
                                    maxLength={2000}
                                    disabled={isSubmitting}
                                />
                                <Field
                                    placeholder="Please enter technical name"
                                    type="text"
                                    component={AntInput}
                                    name="technicalname"
                                    size="large"
                                    label="technical name"
                                    maxLength={1000}
                                    disabled={isSubmitting}
                                />
                                <Field
                                    placeholder="Please enter reference link"
                                    type="text"
                                    component={AntInput}
                                    name="ref"
                                    size="large"
                                    label="reference link"
                                    maxLength={1000}
                                    disabled={isSubmitting}
                                />
                                <ButtonSubmitWrapperStyled
                                    block
                                    htmlType="submit"
                                    size="large"
                                >
                                    Submit
                                </ButtonSubmitWrapperStyled>
                            </Form>
                        </FormInputWrapperStyled>
                    </Space>
                </Modal>
            )}
        </Formik>
    );
};

AddNewRecord.propTypes = {
    visible: PropTypes.bool.isRequired,
    handleClose: PropTypes.func.isRequired,
};

export default AddNewRecord;
