import React from 'react'
import shield from "../assests/shield.svg";
import paymentInfo from "../assests/paymentinfo.json";
import PaymentCard from "../../../../components/payment-card";

export default function PackageContainer({setSelectedPlan}) {
    return (
        <section className="getting-started__pricing">
        <div className="container">
          <div className="getting-started__pricing-heading">
            <img src={shield}></img>
            <h1>
              Sign up today, and you’ll receive up to $12,870 in free ad
              credits!
            </h1>
          </div>
          <div className="getting-started__pricing-body">
            <div className="row">
              {paymentInfo.length > 0 &&
                paymentInfo.map((payment, index) => (
                  <div className="col-lg-3 col-md-6 col-sm-6 col-xs-12 mt-3" key={index}>
                    <PaymentCard
                      data={payment}
                      setSelectedPlan={setSelectedPlan}
                    />
                  </div>
                ))}
            </div>
          </div>
        </div>
      </section>
     
    )
}
